@import url("https://fonts.googleapis.com/css2?family=Raleway:wght@600&display=swap");

html,
body,
#root,
.app {
  width: 100%;
  height: 100%;
}
.app {
  background-color: #e5e5e5;
  font-family: 'Raleway', sans-serif;
  font-weight: 600;
  font-size: 14px;
}
.container {
  height: 100%;
  margin: 0 auto;
}
@media(min-width: 1440px) {
  .container {
    width: 1400px;
  }
}
